.event-section-button {
    background: #1D4169;
    border-radius: 40px;
    width: 240px;
    height: 60px;
    display: flex;
    justify-content: space-between;
    text-align: center;
    margin: auto;
}

.event-section-button h2 {
    font-family: 'Poppins', serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: #FFFFFF;
    margin-left: 28px;
    margin-top: 16px;
    text-align: center;
}

.event-section-button svg {
    margin-top: 20px;
    text-align: center;
    margin-right: 20px;
}