.input-with-button {
    position: relative;
    display: inline-block;
}

.input-with-button input[type="text"] {
    padding: 10px;
    border: none;
    border-radius: 5px;
    width: 100%;
}

.input-with-button button[type="submit"] {
    position: absolute;
    top: 5px;
    right: 5px;
    padding: 0 0;
    color: #fff;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    height: 43px;
    width: 43px;
    background: #DF2224 url("../assets/arrow-right.svg");
}

.flex-box {
    display: flex;
    justify-content: space-between;
    background: #FFFFFF;
    border: 1px solid rgba(0, 0, 0, 0.8);
    box-shadow: 0 0 50px rgba(0, 0, 0, 0.4);
    border-radius: 40px;
    height: 60px;
    width: 400px;
    margin: 61px auto auto;
}

.flex-box input {
    border: none;
    height: 60%;
    width: 80%;
    margin: 10px 10px 10px 27px;
}

input:focus {
    outline: none;
}

.flex-box button {
    background: #DF2224;
    width: 43px;
    border: none;
    height: 43px;
    border-radius: 50%;
    margin: 8px;
}

.flex-box button div {
    background-image: url("../assets/arrow-right.svg");
    width: 20px;
    height: 20px;
    margin-left: 12px;
}

.flex-box-2 {
    display: flex;
    justify-content: flex-end;
}