.logo-wrapper {
    display: flex;
    margin: auto;
    width: 190px;
    height: 60px;
}

.logo-image {
    min-height: 40px;
    max-height: 60px;
    margin-right: 16px;
}

.logo-title {
    display: flex;
    flex-direction: column;
    max-height: 47px;
    margin-top: 6px;
}

.lead-image {
    max-height: 33px;
    min-height: 23px;
}

.advisors-image {
    max-height: 9px;
    min-height: 6px;
    margin-right: 0;
    margin-left: auto;
}

@media (max-width: 788px){
    .logo-wrapper {
        width: 128px;
        height: 40px;
    }

    svg {
        height: 23px;
    }

    .logo-image {
        margin-right: 11px;
    }
}
