* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html, body {
    height: 100%;
}

#root {
    min-height: 100%;
    display: flex;
    flex-direction: column;
}

body {
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
}

.credits {
    color: #FFFFFF;
    background: #1D8888;
    text-align: center;
    padding: 10px 0;
    font-size: 14px;
    font-weight: 300;
}

header {
    min-height: 213px;
}

main {
    flex: 1 1 auto;
    /*background: red;*/
    min-height: 545px;
}

footer {
    height: 192px;
    background: #162C4E;
}